<template>
  <div>
    <span>Ваш баланс: </span>
    <span :style="balanceColor" >{{balance}}</span>
  </div>
</template>

<script>
export default {
  name: 'BudgetListTotalBalance',
  props: {
    values: {
      type: Array,
      default: () => [0],
    },
  },
  computed: {
    balance() {
      if (!this.values.length) return 0;
      // eslint-disable-next-line no-param-reassign,no-return-assign
      const myBalance = this.values.reduce((acc, value) => acc += value);
      return myBalance;
    },
    balanceColor() {
      if (!this.balance) return 'color: black';
      return this.balance > 0 ? 'color: green' : 'color: red';
    },
  },
};
</script>

