<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="8" md="6" class="ma-auto ">
        <v-card color="">
          <v-card-title class="justify-center pb-0">
            <v-row>
              <v-col cols="12" class="d-flex justify-center pb-0">
                <BudgetListTotalBalance
                  v-bind:values="getValues"
                ></BudgetListTotalBalance>
              </v-col>
              <BudgetListSortButtons
                v-on:onSort="onSort"
              ></BudgetListSortButtons>
            </v-row>
          </v-card-title>
          <v-card-text>
            <v-container >
              <transition-group tag="div" name="list" :leave-to-class="isLastElementComputed">
                <template v-for="list in getLists">
                  <BudgetListItem
                    v-bind:sortType="sortType"
                    v-bind:key="list.id"
                    v-bind:list="list"
                    v-on:onDelClick = "onDelClick"
                  ></BudgetListItem>
                </template>
              </transition-group>
            </v-container>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';
import BudgetListItem from './BudgetListItem.vue';
import BudgetListTotalBalance from './BudgetListTotalBalance.vue';
import BudgetListSortButtons from './BudgetListSortButtons.vue';

export default {
  name: 'AppBudgetList',
  data: () => ({
    sortType: 'all',
    isLastElement: '',
  }),
  components: {
    BudgetListItem,
    BudgetListTotalBalance,
    BudgetListSortButtons,
  },
  methods: {
    onSort(type) {
      this.sortType = type;
    },
    onDelClick(value) {
      this.isLastElementComputed = value;
    },
  },
  computed: {
    ...mapGetters('lists', ['getLists', 'getValues']),
    isLastElementComputed: {
      get() {
        return this.isLastElement;
      },
      set(value) {
        this.isLastElement = value;
      },
    },
  },
  watch: {
    getLists() {
      this.sortType = 'all';
    },
  },
};
</script>

