<template>
    <v-row
      class="align-center text--primary border elevation-1 mb-2"
      v-show="toShow"
      :style="itemsColor"
      :key="list.id"
    >
      <v-col
        cols="12"
        order="last"
        class="font-weight-medium "
      >{{list.comment}}</v-col>
      <v-col order="first"
             class="d-flex">
        <span>{{list.date}}</span>
        <span class="font-weight-bold ml-auto text-no-wrap" :style="myArrowColor"> {{list.value}}
          <v-icon small :style="myArrowColor">{{myArrowType}}</v-icon>
        <v-btn  icon small
                 color="red darken-3"
                 v-on:click="onDeleteBudgetListItem"
                 class="ml-auto"
          >
              <v-icon :style="myArrowColor">mdi-delete</v-icon>
            </v-btn>
          </span>
      </v-col>
    </v-row>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'BudgetListItem',
  data: () => ({
    isShown: true,
  }),
  props: {
    list: {
      type: Object,
    },
    sortType: {
      type: String,
    },
  },
  computed: {
    /*    toShow() {
      return this.sortType === this.list.type || this.sortType === 'all';
    }, */
    toShow: {
      get() {
        return this.isShown;
      },
      set(value) {
        this.isShown = this.sortType === this.list.type || this.sortType === 'all';
        if (!value) this.isShown = value;
      },
    },
    itemsColor() {
      return `background-color: ${this.list.type === 'INCOME' ? 'rgb(194, 247, 192)' : 'rgb(245, 218, 220)'}`;
    },
    myArrowType() {
      return this.list.type === 'INCOME' ? 'mdi-arrow-up' : 'mdi-arrow-down';
    },
    myArrowColor() {
      return this.list.type === 'INCOME' ? 'color: green' : 'color: red';
    },
  },
  watch: {
    sortType() {
      this.toShow = this.sortType;
    },
  },
  methods: {
    ...mapActions('lists', ['deleteListItem']),
    onDeleteBudgetListItem() {
      if (this.$el.nextElementSibling) {
        this.$emit('onDelClick', 'list-leave-to');
      }
      if (this.$el.nextElementSibling === null) {
        this.$emit('onDelClick', 'last-list-leave-to');
      }
      this.deleteListItem(this.list.id);
    },
  },
};
</script>


<style scoped>
  .border {
    border-bottom: solid 1px;
  }
</style>
