<template>
  <v-row class="pb-0">
  <v-col>
    <v-btn
      outlined
      block
      small
      color="error"
      v-on:click="onSortOutcome"
    >Расходы</v-btn>
  </v-col>
  <v-col>
    <v-btn
      outlined
      block
      small
      color="success"
      v-on:click="onSortIncome"
    >Доходы</v-btn>
  </v-col>
    </v-row>
</template>

<script>
export default {
  name: 'BudgetListSortButtons',
  props: {},
  data: () => ({
    prevSort: 'all',
  }),
  methods: {
    defineSortType(type) {
      if (this.prevSort === type) {
        this.prevSort = 'all';
        return 'all';
      }
      this.prevSort = type;
      return type;
    },
    onSortOutcome() {
      this.$emit('onSort', this.defineSortType('OUTCOME'));
    },
    onSortIncome() {
      this.$emit('onSort', this.defineSortType('INCOME'));
    },
  },
};
</script>

