<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="8" md="6" class="ma-auto">
        <v-card class="pa-4 ">
          <v-form
            v-on:submit="addNewListItem"
            v-model="validation"
            ref="form"
          >
            <v-select
            v-bind:append-icon="selectValue === 'OUTCOME' ? 'mdi-minus' : 'mdi-plus'"
            item-color="primary"
            v-model="selectValue"
            :menu-props="{closeOnClick: true}"
            :items="[{text: 'Расход', value: 'OUTCOME'}, {text: 'Доход', value: 'INCOME'}]"
            >
            </v-select>
            <v-text-field
              label="Комментарий"
              v-model="commentValue"
              clearable
              autocomplete="off"
              trim
            ></v-text-field>
            <v-text-field
              label="Сумма"
              autocomplete="off"
              v-model="sumValue"
              v-bind:prefix="selectValue === 'OUTCOME' ? '-' : '+'"
              :rules="sumRules"
              aria-required="true"
              validate-on-blur
            ></v-text-field>
            <v-btn
              outlined
              type="submit"
              color="rgba(127, 26, 195, 1)"
              class="fixOutlinedButton"
            >
              Окей
            </v-btn>
          </v-form>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  name: 'AppForm',
  data: () => ({
    isValidForm: true,
    selectValue: 'OUTCOME',
    commentValue: null,
    sumValue: null,
    sumRules: [
      // eslint-disable-next-line no-unused-vars
      (v) => /^[1-9][.]?[0-9]*$/.test(v) || 'Введите число',
    ],
  }),
  methods: {
    ...mapActions('lists', ['addListItem']),
    idGerenator() {
      return Date.now().toString(36) + Math.random().toString(36).substr(2, 5);
    },
    addNewListItem(e) {
      e.preventDefault();
      this.$refs.form.validate();
      if (!this.sumValue) this.validation = false;
      if (!this.validation) return 0;
      let sum = Number.parseFloat(this.sumValue);
      let mycommentValue = this.commentValue;
      if (!this.commentValue) {
        mycommentValue = this.selectValue === 'OUTCOME' ? 'Расходы' : 'Доходы';
      }
      if (this.selectValue === 'OUTCOME' && sum > 0) sum = -sum;
      if (this.selectValue === 'INCOME' && sum < 0) sum = -sum;
      const newListItem = {
        type: this.selectValue,
        comment: mycommentValue,
        value: sum,
        date: new Date().toLocaleString(),
        id: this.idGerenator(),
      };
      this.addListItem(newListItem);
      this.commentValue = '';
      this.sumValue = null;
      return 1;
    },
  },
  computed: {
    validation: {
      get() {
        return this.isValidForm;
      },
      set(value) {
        this.isValidForm = value;
      },
    },
  },
};
</script>

<style scoped>
  .fixOutlinedButton{
    transition: all 0.5s;
  }
.fixOutlinedButton:hover {
  background-color: rgb(143, 71, 155);
  color: white!important;
}
</style>
