import Vue from 'vue';

const listsStore = {
  namespaced: true,
  state: {
    lists: {/*
      1: {
        type: 'INCOME',
        comment: 'My income Comment',
        value: 100,
        date: new Date().toLocaleString(),
        id: 1,
      },
      2: {
        type: 'OUTCOME',
        comment: 'My outcome Comment',
        value: -50,
        date: new Date().toLocaleString(),
        id: 2,
      },
    */},
  },
  getters: {
    getLists({ lists }) {
      return Object.values(lists);
    },
    getValues({ lists }) {
      if (!Object.values(lists)) return [0];
      // eslint-disable-next-line radix
      return Object.values(lists).reduce((acc, list) => {
        acc.push(list.value);
        return acc;
      }, []);
    },
  },
  mutations: {
    DELETE_LIST_ITEM(state, key) {
      Vue.delete(state.lists, String(key));
    },
    ADD_LIST_ITEM(state, item) {
      const array = [];
      array.push(item);
      Object.values(state.lists).forEach((list) => {
        array.push(list);
      });
      state.lists = array.reduce((acc, list) => {
        acc[list.id] = list;
        return acc;
      }, {});
    },
  },
  actions: {
    deleteListItem({ commit }, key) {
      commit('DELETE_LIST_ITEM', key);
    },
    addListItem({ commit }, item) {
      commit('ADD_LIST_ITEM', item);
    },
  },
};

export default listsStore;
